import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import './global.less'

Vue.config.productionTip = false
Vue.filter('filterStatus', str => {
  let ret
  switch (str) {
    case 'done':
      ret = '已完成'
      break
    case 'yet':
      ret = '未完成'
      break
    case 'doing':
    default:
      ret = '进行中'
  }
  return ret
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
