<template>
  <div id="app">
    <div class="nav">
      <div class="logo">
        <!-- <img alt="Vue logo" src="@/assets/icon_logo.png"> -->
      </div>
      <div class="menu">
        <div class="item" v-for="(obj, index) in navList" :key="index">
          <div class="title" :class="{active : isActive(index) }" @click="show(index)">
            <img :src="require('./assets/' + obj.icon)" />
            {{obj.title}}
            <span>
              <img src="./assets/icon_lower.png" :style="obj.isShow ? '' : 'transform: rotate(180deg)'" />
            </span>
          </div>
          <div v-show="obj.isShow">
            <div v-for="(sub, idx) in obj.children" :key="idx"
              @click="go(sub.link)" :class="{ active: currentLink === sub.link}">
              {{sub.name}}
            </div>
          </div>
        </div>
      </div>
      <MiddleMenu />
    </div>
    <router-view class="container" />
    <router-view name="sliding" />
  </div>
</template>

<script>
import { menu } from '@/JSON'
import MiddleMenu from '@/components/middleMenu'
export default {
  data() {
    return {
      currentLink: this.$route.name,
      navList: menu
    }
  },
  components: {
    MiddleMenu
  },
  mounted() {
    setTimeout(() => {
      this.currentLink = this.$route.name
    }, 500)
  },
  methods: {
    // 判断是否为当前模块
    isActive(i) {
      return this.navList[i].children.some(ele => {
        if (ele.link === this.$route.name) {
          return true
        }
      })
    },
    // 展开菜单
    show(i) {
      this.navList[i].isShow = !this.navList[i].isShow
    },
    // 路由跳转
    go(str) {
      this.$router.push(str)
      this.currentLink = str
    },
  }
}
</script>

<style lang="less">

html {
  height: 100%;
  width: 100%;
}
body {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
  background-color: #212633;
  width: 100%;
  height: 100%;
}
#app {
  color: #8F9EBF;
  width: 100%;
  height: 100%;
  display: flex;
  .container {
    flex-grow: 2;
    margin-left: 188px;
  }
}

// 导航菜单
.nav {
  width: 188px;
  background-color: #282d3d;
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100%;
  z-index: 1;
  .logo {
    width: 100%;
    height: 91px;
    background-color: #292E3B;
    background-image: url('./assets/icon_logo.png');
    background-repeat: no-repeat;
    background-position: center;
  }
  .menu {
    flex-grow: 2;
    height: 50%;

    .item {
      // 一级菜单
      min-height: 56px;
      font-size: 14px;
      .title {
        height: 56px;
        line-height: 56px;
        padding-left: 1rem;
        cursor: pointer;
        font-size: 16px;
        img {
          margin-right: 8px;
          vertical-align: middle;
        }
        > span:last-child {
          width: 40px;
          float: right;
          cursor: pointer;
        }
        &.active {
          color: #fff;
        }
      }

      > div:last-child {
        > div {
          line-height: 56px;
          height: 52px;
          padding-left: 52px;
          border-left: 4px solid transparent;
          &:hover {
            cursor: pointer;
            color: #fff;
          }
          &.active {
            color: #fff;
            border-color: #E64646;
            opacity: 0.8;
            background-image: linear-gradient(270deg, rgba(230,70,70,0.20) 0%, #E64646 100%);
          }
        }
      }
    }
  }
}

</style>
