<template>
  <div class="footer">
    <div v-if="!isShowSub" @click="isShowSub = !isShowSub">
      <img src="@/assets/icon_right.png" />
      展开
    </div>
    <div v-else @click="isShowSub = !isShowSub">
      <img src="@/assets/icon_right.png" style="transform: rotate(180deg)" />
      收起
    </div>
    <div v-if="isShowSub" class="midMenu" :class="{close: !isShowSub}">
      <div class="header">
        <img src="@/assets/icon_search.png" />
        <input placeholder="请输入楼栋/工位/座位号" />
      </div>
      <div class="subMenu">
        <div v-for="(Q, A) in subMenu" :key="A">
          <span @click="showSub(Q)">
            <img v-if="Q.isShow" src="@/assets/icon_lower.png" />
            <img v-else src="@/assets/icon_right.png" />
            {{Q.title}}
          </span>
          <div v-if="Q.isShow">
            <div v-for="(W, S) in Q.children" :key="S">
              <span @click="showSub(W)">
                <img v-if="W.isShow" src="@/assets/icon_lower.png" />
                <img v-else src="@/assets/icon_right.png" />
                {{W.title}}
              </span>
              <div v-if="W.isShow">
                <div v-for="(E, D) in W.children" :key="D">
                  <span @click="() => { showSub(E);goSub(A, S, D)}">
                    <img v-if="E.isShow" src="@/assets/icon_lower.png" />
                    <img v-else src="@/assets/icon_right.png" />
                    {{E.title}}
                  </span>
                  <div v-if="E.isShow">
                    <div v-for="(R, F) in E.children" :key="F" @click="goSub(A, S, D, F)" :class="{ active: currentSub === R }">
                      <span>{{R}}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { subMenu } from '@/JSON'
export default {
  data() {
    return {
      // 是否显示中间菜单
      isShowSub: false,
      currentSub: undefined,
      subMenu
    }
  },
  methods: {
    showSub(obj) {
      if (!obj.isShow) {
        if (obj.children instanceof Array && obj.children.length) {
          obj.isShow = true
        }
      } else {
        obj.isShow = false
      }
    },
    goSub(a,s,d,f) {
      let title = [this.subMenu[a].title]
      title.push(this.subMenu[a].children[s].title)
      title.push(this.subMenu[a].children[s].children[d].title)
      if (typeof(f) !== 'undefined') {
        title.push(this.subMenu[a].children[s].children[d].children[f])
        this.currentSub = this.subMenu[a].children[s].children[d].children[f]
      } else {
        this.$root.$emit('change', title)
      }
    }
  }
}
</script>

<style lang="less" scoped>

  .footer {
    // 底部展开、收起栏
    height: 56px;
    background-color: #32384D;
    line-height: 56px;
    position: relative;
    img {
      vertical-align: middle;
    }
    > div:first-child {
      cursor: pointer;
      text-align: center;
    }
    > div.midMenu {
      // 中间菜单
      width: 300px;
      height: 100%;
      background-image: linear-gradient(180deg, #424B66 0%, #32384D 100%);
      position: fixed;
      top: 0;
      left: 188px;
      // opacity: 0.8;
      transition: all 0.5s;
      &.close {
        transition: all 0.5s;
        // transform: translateX(100%);
        // opacity: 0;
      }

    }
  }
  .midMenu {
  // 中间菜单
  div {
    min-height: 30px;
    line-height: 30px;
  }
  img {
    vertical-align: middle;
    margin-right: 5px;
  }
  .header {
    // 搜索框
    width: 252px;
    border-radius: 2px;
    background: #32384D;
    margin: 22px auto;
    line-height: 40px;
    text-align: center;
    input {
      color: #fff;
      background-color: transparent;
      border: none;
      width: 180px;
    }
  }
  .subMenu {
    background-image: url('../assets/Dividing_line1.png');
    background-repeat: no-repeat;
    background-position: center top;
    color: #8F9EBF;
    font-size: 14px;
    .active {
      color: #fff;
    }
    span {
      cursor: pointer;
      display: block;
      &:hover {
        color: #fff;
        background: #4B5573;
      }
    }
    > div {
      // 一级菜单
      > span img {
        padding-left: 1rem;
      }
      > div {
        // 二级菜单
        > div {
          > span img {
            padding-left: 2rem;
          }
          > div {
            // 三级菜单
            > div {
              > span img {
                padding-left: 3rem;
              }
              > div {
                > div {
                  span {
                    padding-left: 5rem;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>