// 工位地图模式坐标列表
  export const seatList = function() {
    const source = []
    for (let i = 0; i < 3; i++) {
      source.push({
        xy: [64, 66, 335 + i*65, 192]
      })
    }
    for (let i = 0; i < 6; i++) {
      source.push({
        xy: [64, 66, 335 + i*65, 260]
      })
    }
    for (let i = 0; i < 6; i++) {
      source.push({
        xy: [64, 71, 335 + i*65, 327]
      })
    }
    for (let i = 0; i < 6; i++) {
      source.push({
        xy: [64, 69, 335 + i*65, 400]
      })
    }
    for (let i = 0; i < 5; i++) {
      source.push({
        xy: [64, 67, 335 + i*65, 470]
      })
    }
    for (let i = 0; i < 6; i++) {
      source.push({
        xy: [64, 82, 335 + i*65, 539]
      })
    }
    for (let i = 0; i < 3; i++) {
      source.push({
        xy: [64, 95, 788 + i*65, 436]
      })
    }
    for (let i = 0; i < 3; i++) {
      source.push({
        xy: [64, 68, 788 + i*65, 533]
      })
    }
    return source.filter(() => {
      return Math.round(Math.random() * 10) % 2 === 0
    })
  }


  // 设备计划列表
  export const devicePlayList = function() {
    const ret = []
    for (let i = 0; i < 10; i++) {
      let r = Math.floor(Math.random() * 100) % 3
      let d = new Date()
      d.setDate(d.getDate() - r)
      ret.push({
        i: i + 1,
        status: r === 0 ? 'done' : (r === 1 ? 'not' : 'doing'),
        date: d.toLocaleDateString(),
        time1: '12:' + (+new Date() % 60),
        time2: '18:' + new Date().getMinutes(),
        name: '南一区电脑巡检',
        point: r * i % 10 + 1
      })
    }
    return ret
  }