import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    redirect: '/work'
  },
  {
    path: '/work',
    name: 'work',
    components: {
      default: () => import('@/views/work/work.vue'),
      sliding: () => import('@/components/work/workAreaSliding.vue')
    }
  },
  {
    path: '/worklist',
    name: 'worklist',
    component: () => import('@/views/work/worklist.vue')
  },
  {
    path: '/workanalysis',
    name: 'workanalysis',
    component: () => import('@/views/work/workanalysis.vue')
  },
  {
    path: '/device',
    name: 'device',
    components: {
      default: () => import('@/views/device/device.vue'),
      sliding: () => import('@/components/work/workAreaSliding.vue')
    },
  },
  {
    path: '/devicelist',
    name: 'devicelist',
    component: () => import('@/views/device/devicelist.vue'),
  },
  {
    path: '/deviceanalysis',
    name: 'deviceanalysis',
    component: () => import('@/views/device/deviceanalysis.vue')
  },
  {
    path: '/deviceplan',
    name: 'deviceplan',
    components: {
      default: () => import('@/views/device/deviceplan.vue'),
      sliding: () => import('@/components/device/deviceplanSliding.vue')
    }
  },
  {
    path: '/deviceplanlist',
    name: 'deviceplanlist',
    component: () => import('@/views/device/planlist.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
